<template>
  <div id="index">
    <el-container>
      <el-aside width="auto">
        <el-menu default-active="/index/jurisdiction" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" @select="handleSelect"
                 :router="true" :collapse="isCollapse" :unique-opened="true" background-color="#20222A"
                 text-color="rgba(255,255,255,.7)" active-text-color="#fff">
          <li class="el-log"><span>Music</span></li>
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span slot="title">导航一</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/index/jurisdiction">选项1</el-menu-item>
              <el-menu-item index="1-2">选项2</el-menu-item>
              <el-menu-item index="1-3">选项3</el-menu-item>
            </el-menu-item-group>
            <el-submenu index="1-4">
              <span slot="title">选项4</span>
              <el-menu-item index="1-4-1">选项1</el-menu-item>
            </el-submenu>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span slot="title">导航二</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="2-1">选项1</el-menu-item>
              <el-menu-item index="2-2">选项2</el-menu-item>
              <el-menu-item index="2-3">选项3</el-menu-item>
            </el-menu-item-group>
            <el-submenu index="2-4">
              <span slot="title">选项4</span>
              <el-menu-item index="2-4-1">选项1</el-menu-item>
            </el-submenu>
          </el-submenu>
          <el-menu-item index="3">
            <i class="el-icon-document"></i>
            <span slot="title">导航三</span>
          </el-menu-item>
          <el-menu-item index="4">
            <i class="el-icon-setting"></i>
            <span slot="title">导航四</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-container :class="collapse ? collapse = isCollapse ? 'headerOpen':'headerClose' : ''" :style="isCollapse?'left:64px':'left:230px'">
        <el-header height="50px">
          <div class="el-header-nav">
            <ul class="el-ul el-ul-left">
              <li @click="isHeader()" :title='isCollapse?"打开侧栏":"关闭侧栏"'><a href="javascript:;"><i
                class="el-icon-menu"></i></a></li>
              <li><a href="javascript:;"><i class="el-icon-news"></i></a></li>
              <li><a href="javascript:;"><i class="el-icon-service"></i></a></li>
            </ul>
            <ul class="el-ul el-ul-right">
              <li><a href="javascript:;"><i class="el-icon-bell"></i></a></li>
              <li><a href="javascript:;"><i class="el-icon-date"></i></a></li>
              <li>
                <a href="javascript:;">
                  <el-dropdown>
                    <span class="el-dropdown-link">
                      下拉菜单<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item>黄金糕</el-dropdown-item>
                      <el-dropdown-item>狮子头</el-dropdown-item>
                      <el-dropdown-item>螺蛳粉</el-dropdown-item>
                      <el-dropdown-item disabled>双皮奶</el-dropdown-item>
                      <el-dropdown-item divided>蚵仔煎</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </a>
              </li>
            </ul>
          </div>
        </el-header>
        <div>
          <el-tabs v-model="editableTabsValue2" type="card" closable @tab-remove="removeTab" @tab-click="clickTab">
            <el-tab-pane v-for="(item, index) in editableTabs2" :key="item.name" :label="item.title" :name="item.name">
              <router-view></router-view>
            </el-tab-pane>
          </el-tabs>
        </div>
        <!--<el-main style="padding: 15px">
          <router-view></router-view>
        </el-main>-->
      </el-container>
    </el-container>
  </div>
</template>

<script>
  export default {
    name: "index",
    data() {
      return {
        isCollapse: false,
        collapse: false,
        //动态tabs
        editableTabsValue2: '2',
        editableTabs2: [{
          title: 'Tab 1',
          name: '1',
          content: 'Tab 1 content'
        }, {
          title: 'Tab 2',
          name: '2',
          content: 'Tab 2 content'
        }],
        tabIndex: 2
      }
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      isHeader() {
        if (!this.collapse) {
          this.collapse = !this.collapse;
        }
        this.isCollapse = !this.isCollapse;
      },
      //动态tabs
      addTab(targetName) {
        let newTabName = ++this.tabIndex + '';
        this.editableTabs2.push({
          title: 'New Tab',
          name: newTabName,
          content: 'New Tab content'
        });
        this.editableTabsValue2 = newTabName;
      },
      removeTab(targetName) {
        let tabs = this.editableTabs2;
        let activeName = this.editableTabsValue2;
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1];
              if (nextTab) {
                activeName = nextTab.name;
              }
            }
          });
        }
        this.editableTabsValue2 = activeName;
        this.editableTabs2 = tabs.filter(tab => tab.name !== targetName);
      },
      clickTab(targetName){
        console.log(targetName)
      }
    }
  }
</script>

<style lang="scss">
  #index{
    .el-container {
      .el-aside {
        position: absolute;
        height: 100%;
        display: flex;
        .el-menu {
          -moz-user-select: -moz-none;
          -khtml-user-select: none;
          -webkit-user-select: none;
          .el-menu-item {
            height: 44px;
            line-height: 44px;
          }
          .is-opened {
            .el-submenu__title {
              border-left: 3px solid #3a8ee6!important;
            }
          }
          .el-menu--inline {
            .el-submenu {
              .el-submenu__title {
                border-left: 0 solid transparent!important;
              }
            }
            .el-menu-item {
              border-left: 0;
            }
          }
          .el-submenu {
            .el-submenu__title {
              border-left: 3px solid transparent;
            }
            .el-submenu__title:hover {
              border-left-color: #3a8ee6;
            }
            .el-submenu__title:first-child {
              height: 44px;
              line-height: 44px;
            }
            .el-menu-item-group {
              .el-menu-item-group__title {
                display: none;
              }
            }
          }
          .el-menu-item {
            border-left: 3px solid transparent;
          }
          .el-menu-item:hover {
            border-left-color: #3a8ee6;
          }
        }
        .el-menu-vertical-demo:not(.el-menu--collapse) {
          width: 230px;
        }
        .el-log {
          height: 49px;
          text-align: center;
          line-height: 49px;
          color: rgba(255, 255, 255, .8);
          span {
            font-weight: 600;
          }
        }
      }
      .el-header {
        padding: 0;
        z-index: 999;
        background-color: #fff;
        box-sizing: border-box;
        border-bottom: 1px solid #f6f6f6;
        .el-header-nav {
          height: 50px;
          position: relative;
        }
        .el-ul {
          padding: 0 10px;
          li {
            height: 50px;
            float: left;
            line-height: 50px;
            margin: 0 20px;
            a {
              color: #000;
            }
          }
        }
        .el-ul-left {
          float: left;
        }
        .el-ul-right {
          float: right;
        }
      }
      .is-vertical {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 998;
        width: auto;
        overflow: hidden;
        overflow-y: auto;
        box-sizing: border-box;
        .el-tabs{
          .el-tabs__header{
            border-bottom: none;
            background-color: #fff;
            box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .1);
            .el-tabs__item.is-active{
              border-bottom-color: transparent;
            }
            .el-tabs__nav{
              border: none;
              border-radius: 0;
              border-right: 1px solid #e4e7ed;
            }
          }
          .el-tabs__content{
            padding: 0 15px;
            overflow: auto;
          }
        }
      }
      .headerOpen {
        -webkit-animation: open .45s;
        -o-animation: open .45s;
        animation: open .45s;
      }
      .headerClose {
        -webkit-animation: close .35s;
        -o-animation: close .35s;
        animation: close .35s;
      }
    }
    @keyframes open {
      from {left: 230px}
      to {left: 64px}
    }

    @keyframes close {
      from {left: 64px}
      to {left: 230px}
    }
  }
</style>



